.icon-block {
  padding: 0 15px; }

.icon-block .material-icons {
  font-size: inherit; }

.login-page-wrapper {
  display: table;
  margin: auto; }

.login-form {
  width: 280px; }

.margin {
  margin: 0 !important; }

.app-name {
  color: #000;
  text-decoration: none; }

.signup-form {
  width: 450px; }

.search {
  width: 400px; }

.sidebar {
  width: 240px; }

.user-details {
  background: url("user-bg.jpg");
  background-size: cover;
  margin-bottom: 15px;
  padding: 15px 0 0 15px;
  line-height: 44px; }

#left-sidebar-nav {
