.profile-image {
  width: 50px;
  height: 50px; }

ul.side-nav.leftside-navigation .profile-btn {
  margin: 0;
  text-transform: capitalize;
  padding: 0;
  text-shadow: 1px 1px 1px #444;
  font-size: 15px; }

ul.side-nav.leftside-navigation li a {
  font-size: 14px;
  font-weight: 400; }

.side-nav a {
  line-height: 42px;
  height: 42px; }

.main {
  padding-left: 240px; }

@media only screen and (max-width: 993px) {
  .main {
    padding-left: 0; } }

.main-section {
  padding-left: 10px;
  padding-right: 10px; }

#profile .dropdown {
  width: 128px;
  position: absolute;
  top: 57px;
  left: 101.234px;
  opacity: 1; }

.user-roal {
  color: #fff;
  margin-top: -16px;
  font-size: 13px;
  text-shadow: 1px 1px 1px #444; }

ul.side-nav.leftside-navigation li.active {
  background-color: rgba(0, 0, 0, 0.04); }

ul.side-nav.leftside-navigation li a {
