  left: -170px;
  top: 15px; }

#profile-page-header .card-image {
  height: 250px; }

#profile-page-header .card-profile-image {
  width: 110px;
  position: absolute;
  top: 190px;
  z-index: 1;
  left: 40px;
  cursor: pointer;
  margin: 0; }

.document-card .card .btn-permission {
  width: 65px;
  height: 65px;
  font-weight: 600;
  font-size: 1.0rem;
  line-height: 65px;
  margin: 10px;
  position: absolute;
  top: 0;
  letter-spacing: 0; }

.document-card ul.card-action-buttons {
  margin: -26px 10px 0 0;
  text-align: right; }

.document-card ul.card-action-buttons li {
  display: inline-block;
  padding-left: 5px; }

.document-card .card .card-content .card-title, .product-card .card .card-reveal .card-title {
  font-size: 1.1rem;
  line-height: 1.6rem;
  font-weight: 400; }



